import Stats from 'stats.js';
import getUserMedia from 'getusermedia';
var publicPath = process.env.BASE_URL;
var cv = null;
export default {
  name: 'app',
  data: function data() {
    return {
      videoWidth: window.innerWidth,
      videoHeight: window.innerHeight,
      stream: null,
      stats: null,
      canvasCtx: null,
      faceClassifier: null,
      detectMat: null,
      videoCap: null,
      faceVect: null,
      processTimer: null,
      msg: ''
    };
  },
  methods: {
    startCamera: function startCamera() {
      var _this = this;

      var constraints = {
        audio: false,
        video: {
          width: {
            facingMode: 'environment',
            min: 960,
            ideal: window.innerWidth,
            max: 1920
          },
          height: {
            min: 540,
            ideal: window.innerHeight,
            max: 1080
          }
        }
      };
      getUserMedia(constraints, function (err, s) {
        if (err) {
          _this.setMsg('getUserMedia failed', 'warn');

          console.warn(err);
          return;
        }

        _this.setMsg('getUserMedia success');

        _this.$refs.videoSrc.srcObject = s;

        _this.$refs.videoSrc.play();

        _this.stream = s;
        console.log(s);
      });
    },
    stopCamera: function stopCamera() {
      if (!this.stream) return;
      this.setMsg('stopCamera');
      this.stopVideoProcessing();
      this.$refs.videoSrc.pause();
      this.$refs.videoSrc.srcObject = null;
      this.stream.getVideoTracks()[0].stop();
      this.stream = null;
    },
    onSourceReady: function onSourceReady() {
      this.setMsg('onSourceReady.');
      this.videoWidth = this.$refs.videoSrc.videoWidth;
      this.videoHeight = this.$refs.videoSrc.videoHeight;
      this.canvasCtx = this.$refs.canvas.getContext('2d');
      console.log("get window size " + window.innerWidth + " : " + window.innerHeight);
      this.startVideoProcessing(); //disable opencv load
      // if (window.cv) {
      //   console.log(this.videoWidth, this.videoHeight)
      //   cv = window.cv
      //   this.startVideoProcessing()
      // } else {
      //   this.loadOpenCv()
      // }
    },
    initStats: function initStats() {
      this.stats = new Stats();
      this.stats.showPanel(0);
      this.$refs.app.appendChild(this.stats.dom);
    },
    loadOpenCv: function loadOpenCv() {
      var _this2 = this;

      if (!window.WebAssembly) {
        this.setMsg("Your web browser doesn't support WebAssembly.", 'warn');
        return;
      }

      this.setMsg('loading OpenCv.js');
      var script = document.createElement('script');
      script.type = 'text/javascript';
      script.async = 'async';
      script.src = "".concat(publicPath, "libs/opencv.js");
      document.body.appendChild(script);

      script.onload = function () {
        _this2.setMsg('OpenCV.js is loaded.');
      };

      window.Module = {
        wasmBinaryFile: "".concat(publicPath, "libs/opencv_js.wasm"),
        // for wasm mode
        preRun: function preRun() {
          _this2.setMsg('loading haarcascade_frontalface_default.xml');

          window.Module.FS_createPreloadedFile('/', 'haarcascade_frontalface', "".concat(publicPath, "data/haarcascade_frontalface_default.xml"), true, false);
        },
        _main: function _main() {
          _this2.setMsg('OpenCV.js is ready.');

          cv = window.cv; // console.log(cv.getBuildInformation())

          _this2.startVideoProcessing();
        }
      };
    },
    startVideoProcessing: function startVideoProcessing() {
      // this.setMsg('startVideoProcessing.')
      if (!this.stream) {
        this.setMsg('Please startup your webcam.', 'warn');
        return;
      }

      var that = this;
      setTimeout(function () {
        that.canvasCtx.drawImage(that.$refs.videoSrc, 0, 0, that.videoWidth, that.videoHeight); // let imgData = that.canvasCtx.getImageData(0, 0, that.videoWidth * 0.2, that.videoHeight * 0.2);
        // console.log("image data " + imgData.data);

        that.startVideoProcessing(); // console.log('test');
      }, 30); //disable opencv function
      // this.$refs.videoSrc
      // this.faceClassifier = new cv.CascadeClassifier()
      // this.faceClassifier.load('haarcascade_frontalface')
      // this.srcMat = new cv.Mat(this.videoHeight, this.videoWidth, cv.CV_8UC4)
      // this.detectMat = new cv.Mat()
      // this.videoCap = new cv.VideoCapture(this.$refs.videoSrc)
      // this.faceVect = new cv.RectVector()
      // this.initStats()
      // this.processTimer = requestAnimationFrame(this.processVideo)
    },
    stopVideoProcessing: function stopVideoProcessing() {
      cancelAnimationFrame(this.processTimer); // // this.stats.end()
      // if (this.faceClassifier && !this.faceClassifier.isDeleted()) {
      //   this.faceClassifier.delete()
      // }
      // this.srcMat.delete()
      // this.detectMat.delete()
      // this.faceVect.delete()
      // this.canvasCtx.clearRect(0, 0, this.videoWidth, this.videoHeight)
    },
    processVideo: function processVideo() {
      if (!this.$refs.videoSrc) {
        this.setMsg('Video stream not found.', 'warn');
        return;
      }

      this.stats.begin();
      this.videoCap.read(this.srcMat);
      cv.cvtColor(this.srcMat, this.detectMat, cv.COLOR_RGBA2GRAY, 0);
      cv.pyrDown(this.detectMat, this.detectMat);
      cv.pyrDown(this.detectMat, this.detectMat);
      this.faceClassifier.detectMultiScale(this.detectMat, this.faceVect, 1.1, 3, 0);
      this.drawFace();
      this.stats.end();
      this.processTimer = requestAnimationFrame(this.processVideo);
    },
    drawFace: function drawFace() {
      this.canvasCtx.clearRect(0, 0, this.videoWidth, this.videoHeight);

      for (var i = 0; i < this.faceVect.size(); ++i) {
        var rect = this.faceVect.get(i);

        if (rect.width > 0 && rect.height > 0) {
          this.canvasCtx.lineWidth = 2;
          this.canvasCtx.strokeStyle = 'red';
          this.canvasCtx.strokeRect(rect.x * 4, rect.y * 4, rect.width * 4, rect.height * 4);
        }
      }
    },
    setMsg: function setMsg(msg) {
      var type = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'log';
      this.msg = msg;
      console[type](msg);
    }
  },
  mounted: function mounted() {
    console.log("current url : " + process.env.VUE_APP_API_URL);
    this.startCamera();
  },
  beforeDestroy: function beforeDestroy() {
    console.log("destroy");
    this.stopCamera();
  }
};